import React, {useState} from 'react';
import './App.css';
import Header from './components/Header/Header';
import DarkMode from './components/Dark-mode/Dark-mode'
import List from './components/List/List';
import Display from './components/Display/Display';
import Size from './components/Size/Size';
import {versions, project, team} from './put-info-here';


const App = () => {
  const [displayVersion, setDisplayVersion] = useState(versions[0]);
  const [isDarkModeOn, setDarkMode] = useState(false);
  const handleListClick = (e,i) => setDisplayVersion(versions[i])
  const handleDarkModeClick = () => {
    setDarkMode(!isDarkModeOn);
    toggleBodyDark()
  }
  const toggleDarkMode = (isDarkModeOn)? "dark" : "";

  const bodyClassList = document.body.classList;
  const toggleBodyDark = () => {
    (isDarkModeOn)? bodyClassList.remove('dark'): bodyClassList.add('dark') 
  }

  return (
    <div className={`App ${toggleDarkMode}`}>
      <Header team={team}></Header>
      <div className="resp">
        <List list={versions} onClick={handleListClick} darkMode={isDarkModeOn}/>
        <main className="container">
          <DarkMode onClick={handleDarkModeClick} darkMode={isDarkModeOn}/>
          <h2>{project}</h2>
          <Display version={displayVersion} darkMode={isDarkModeOn}/>
        </main>
      </div>
      <Size size={displayVersion.size}></Size>
    </div>
  );
}

export default App;
 