import React from 'react';
import './dark-mode.css';

const DarkMode = (props) => {
    const toggleDarkMaode = (props.darkMode)? "active" : "";

    return (
    <button onClick={()=>props.onClick()} data-tooltip="Toggle Dark mode" className={`dark-mode ${toggleDarkMaode}`}>Dark mode</button> 
    )
}

export default DarkMode;