import React from 'react';
import './compare.css'

const Compare = (props) => {
    const message = (!props.srcStat)? "View Previous" : "Back"
    const button = (props.isOlderAvailable)? (<button className="viewPrevious" onClick={(e)=>props.onClick(e)}>{message}</button>) : ""

    return (
        button
    )
}
export default Compare;