import React from 'react';
import './header.css';
import aLogo from '../../img/Activatr_logo_white.png';
import marksLogo from '../../img/marks-logo.png'

const Header = (props) => {
    let logo = '';
    
    switch(props.team){
        case 'activatr':
            logo = {
                name: 'activatr',
                src: aLogo
            };
            break;
        case 'marks':
            logo = {
                name: 'marks',
                src: marksLogo
            };
            break;
        default: logo = {};
    }

    return (
        <header>
         <img className={logo.name + "-logo"} src={logo.src} alt={logo.name + " logo"} />  
        </header>
    )
}
export default Header;