import React, { useState } from 'react';
import './list.css'

const List = (props) => {
    const [isdrawerOpen, setDrawerStatus] = useState(false);
    const uList = (isdrawerOpen)? "open" : "";
    const handleButtonClick = () => setDrawerStatus(!isdrawerOpen);
    const handleListClick = () => setDrawerStatus(false);
    const toggleDarkmode = (props.darkMode)? "dark" : "";

    const list = props.list.map((item, i) => {
        return (<li key={i} onClick={(e)=>{handleListClick(); props.onClick(e,i); } } role="option">{item.name}</li>)
    })
    
    return(
        <nav className={toggleDarkmode}>
            <button onClick={()=>handleButtonClick()} className="version-list_toggle-button">
                Select Design <span className="version-list_toggle-indicator">{(isdrawerOpen)? "-" : "+"}</span>
            </button>
            <ul className={`version-list ${uList}`} role="select">
                {list}
            </ul>
        </nav>
    )
}

export default List;
;