import React, {useState} from 'react';
import './display.css';
import Compare from '../Compare/Compare';

const buildIframe = (version, isOld) => {
    const toggleSrc = (version.oldSrc && isOld) ? version.oldSrc: version.src;
    
    return(
        <div>
            <iframe className="resp-iframe" frameBorder="0" scrolling="no" src={process.env.PUBLIC_URL + toggleSrc} title="version"></iframe>
        </div>
        )
}

const Display = ({version}) => {
    const [isThisOldSrc, setThisSrc] = useState((!version.oldSrc));
    const togglePrevHidden = (version.oldSrc && isThisOldSrc) ? "": "hidden"
    let currentVersion = buildIframe(version, isThisOldSrc);

    const handleClick = () => { 
        setThisSrc(!isThisOldSrc)
        currentVersion = buildIframe(version, isThisOldSrc)
    }

    return (
        <div className="resp-container">
            <div className="resp-container__top">
                <h4 className="display__title">{version.name} <span className={`isPrev ${togglePrevHidden}`}>(previous)</span></h4>
                <Compare srcStat={isThisOldSrc} isOlderAvailable={(!!version.oldSrc)} onClick={handleClick}></Compare>
            </div>
            {currentVersion}
        </div>
        
    )
}
export default Display;